package adventOfCode.year2022

import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO
import outwatch.dsl._
import adventOfCode.{AocDay, VisualDebugger}
import cats.effect.IO

object Day04 extends AocDay {
  val day = 4

  def parsePairRanges(line: String) = {
    val Array(first, second) = line.split(",").map { rangeStr =>
      val Array(start, end) = rangeStr.split("-").map(_.toInt)
      start.to(end)
    }

    ElfPair(first, second)

  }

  case class ElfPair(elf1: Range.Inclusive, elf2: Range.Inclusive)

  def containsOneOrTheOther(elfPair: ElfPair): Boolean = {
    val e1 = elfPair.elf1
    val e2 = elfPair.elf2
    e1.contains(e2.start) && e1.contains(e2.end) || e2.contains(e1.start) && e2.contains(e1.end)
  }

  def overlaps(elfPair: ElfPair): Boolean = {
    val e1 = elfPair.elf1
    val e2 = elfPair.elf2
    e1.contains(e2.start) || e1.contains(e2.end) || e2.contains(e1.start) || e2.contains(e1.end)
  }

  def rangeToStr(range: Range.Inclusive) = s"${range.start}-${range.end}"

  def solvePart1(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val pairRanges = input.linesIterator
      .map(parsePairRanges)
      .toList
      .map(ep => ep -> containsOneOrTheOther(ep))

    visualizePart1(pairRanges, "contains")

    pairRanges.count(_._2).toString

  }

  def solvePart2(input: String)(implicit visualDebugger: VisualDebugger) = IO {
    val pairRanges = input.linesIterator
      .map(parsePairRanges)
      .toList
      .map(ep => ep -> overlaps(ep))

    visualizePart1(pairRanges, "overlaps")

    pairRanges.count(_._2).toString
  }

  import adventOfCode.util.ui.Helpers.FormattedTable._

  def visualizePart1(pairRanges: List[(ElfPair, Boolean)], boolLabel: String)(implicit
    visualDebugger: VisualDebugger,
  ) =
    visualDebugger.render(
      div(
        h3(cls := "font-medium leading-tight text-2xl mt-0 mb-2", "Pairs"),
        table(
          thead(
            tr(
              th("Pair #"),
              th("Elf 1"),
              th("Elf 2"),
              th("contains"),
            ),
          ),
          tbody(
            pairRanges.zipWithIndex.map { case ((ElfPair(elf1, elf2), contains), idx) =>
              tr(
                td(idx + 1),
                td(rangeToStr(elf1)),
                td(rangeToStr(elf2)),
                td(contains),
              )
            },
          ),
        ),
      ),
    )

}
